













































































































































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
import Footer from '@/components/footer.vue'
export default {
	mixins:[mixin],
	components:{
		'v-footer':Footer
	},
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			vipawardlist:[
				{
					id:1,
					title:'VIP1',
					desc:'VIP1的权益',
					current:true
				},
				{
					id:2,
					title:'VIP2',
					desc:'VIP2的权益',
					current:false
				},
				{
					id:3,
					title:'VIP3',
					desc:'VIP3的权益',
					current:false
				}
			],
			is_show:true,
		};
	},
	methods:{
		async init(){
			Utils.loading('正在加载')
			let response = await api.initIdentify({uid:this.userInfo.uid})
			console.log(response)
			
			if(response.status == 1){
				Utils.loaded()
				let data = response.data;
				this.$store.commit('set_userInfo', data);
				this.userInfo = data
			}
			
			
			if(this.userInfo.identity == 1){
				if(!this.is_show){
					uni.showTabBar({
						
					})
					
				}else{
					uni.hideTabBar({
						
					})
				}
			}else{
				this.is_show = false
				uni.hideTabBar({
					
				})
			}
			
		},
		navigateto(type){
			console.log(type)
			uni.navigateTo({
				url:type
			})
		},
		changeVip(e) {
			let vipid = e.detail.current
			this.currenid = vipid
		},
		showModel(){
			let isshow = this.is_show
			if(isshow){
				this.is_show = false
				uni.showTabBar({
					
				})
			}else{
				this.is_show = true
			}
		},
		scanVerify(){
			uni.scanCode({
				onlyFromCamera: true,
				success:function(res){
					console.log(res)
				}
			})
		}
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	}
}
